.apod-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
}

.apod-result h2 {
  border: 2px solid white;
  padding: 10px;
  border-radius: 100px;
}

.apod-result img {
  border: 2px solid white;
  filter: drop-shadow(0px 0px 7px rgb(255, 255, 255));
}
